@import url('https://fonts.googleapis.com/css2?family=Inria+Sans:wght@700&display=swap');

html[data-theme='light'] {
    --body-background-color: #ffffff;
    --sidebar-background-color: #f1f6ff;
    --logo-title-color: #414A6D;
    --profile-menu-font-color: #373f5d;
    --handler-color: #454C77;
    --arrow-scale-color: #8F93AD;
    --background-user-info: #f1f6ff;
    --user-info-main-box-shadow: 0 0 10px rgb(130, 130, 130);
    --contacts-background: #f1f6ff;
    --contact-title-color: #414A6D;
    --premium-background-color: #F1F6FF;
    --premium-box-shadow: 0 0 2px #020202;
    --main-logo-color: #594A6D;
    --partnership-elements-background: #F1F6FF;
    --button-premium-disabled: #93d1eb;
    --password-recovery-background: #F1F6FF;
    --login-background: #F1F6FF;
    --button-back-color: #454C77;
}

html[data-theme='dark'] {
    /* --body-background-color: linear-gradient(180deg, #313B58 0%, #252F47 100%); */
    --body-background-color: #303A58;
    --sidebar-background-color: #252F47;
    --logo-title-color: #ffffff;
    --profile-menu-font-color: #6b7492;
    --handler-color: #ffffff;
    --arrow-scale-color: #828CAE;
    --outlined-name-focus: #ffffff;
    --background-user-info: #252F47;
    --user-info-main-box-shadow: 0 0 20px #252F47;
    --contacts-background: #336B92;
    --contact-title-color: #ffffff;
    --premium-background-color: #252F47;
    --premium-box-shadow: none;
    --main-logo-color: #ffffff;
    --partnership-elements-background: #252F47;
    --button-premium-disabled: #303A58;
    --password-recovery-background: #252F47;
    --login-background: #252F47;
    --button-back-color: #53B9EA;
}

a:focus,
a:active {
	outline: none;
}


a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

a:-webkit-any-link {
    color: red;
}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   

.main {
    width: 100%;
    display: flex;
    flex-direction: row;
}

*{
    margin: 0;
    padding: 0;
}

.main__info {
    background-color: var(--body-background-color);
    width: 100%;
    height: 100vh;
    transition: background-color 100ms linear;
}

.sidebar {
    background-color: var(--sidebar-background-color);
    min-width: 280px;
    font-size: 15px;
    transition: background-color 100ms linear;
}

.sidebar__close {
    min-width: 100px;
    font-size: 0;
}

.sidebar__close .logo__title {
    font-size: 30px;
}

.sidebar__close .sidebar__btns {
    align-items: center;
}

.sidebar__close .profile__menu {
    align-items: center;
}

.sidebar__close .profile__menu div {
    font-size: 0;
}

.sidebar__close .profile__menu div ion-icon {
    font-size: 14px;
}

.sidebar__close .change__scale {
    justify-content: center;
    font-size: 20px;
    margin-top: 50px;
    width: 100%;
    transform: rotate(180deg);
}

.sidebar__close .sidebar__ionicon {
    margin-right: 0;
}

.sidebar__close .small {
    width: 100%;
    margin-left: 0;
    padding-left: 0;
}

.sidebar__btns {
    align-items: stretch;
    display: flex;
    flex-direction: column;
    margin-top: 60px;
}

.sidebar__ionicon {
    width: 20px;
    height: 20px;
    margin-right: 20px;
}

hr {
    background: #5b6792;
    border: none;
    height: 2px;
    margin-top: 2px;
    width: 100%;
}

.logo__title {
    width: 100%;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    font-family: 'Inria Sans', sans-serif;
    margin-top: 30px;
    color: var(--logo-title-color);
}

.profile__menu {
    width: 100%;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 17%;
    margin-top: 20px;
    font-family: 'Inria Sans', sans-serif;
    color: var(--profile-menu-font-color);
}

.profile__menu div {
    display: flex;
    margin-top: 15px;
    cursor: pointer;
}

.profile__menu ion-icon {
    margin-right: 10px;
}

.header {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
}

.handler__img {
    height: 25px;
    width: 25px;
    color: var(--handler-color);
}

.change__scale {
    color: var(--arrow-scale-color);
    display: flex;
    justify-content: flex-end;
    margin-right: 50px;
    font-size: 20px;
    margin-top: 50px;
    cursor: pointer;
}

.profile {
    width: 100%;
    height: 80%;
}

#outlined-name {
    color: #7D87A9;
    outline: #7D87A9;
}

#outlined-name:focus {
    color: var(--outlined-name-focus);
}

.mail__textfield label {
    border: none;
    outline: none;
    color: #7D87A9;
}

.mail__textfield::placeholder {
    color: #7D87A9;
}

.all__profile__fields {
    display: flex;
}

.profile__title {
    color: #7D87A9;
    font-family: 'Inria Sans', sans-serif;
    font-size: 20px;
    text-transform: uppercase;
    margin-left: 100px;
    margin-right: 20px;
    margin-top: 40px;
}

.profile__field {
    display: flex;
    align-items: flex-start;
    margin-left: 120px;
    margin-top: 40px;
}

.avatar {
    display: flex;
    align-items: center;
    margin-top: 40px;
    justify-content: center;
}

.avatar span {
    margin-left: 10px;
    font-size: 15px;
    font-family: 'Inria Sans', sans-serif;
    font-weight: bold;
    color: #7487A9;
}

.user__info {
    font-size: 15px;
    font-family: 'Inria Sans', sans-serif;
    font-weight: normal;
    color: #7487A9;
    display: flex;
    flex-direction: column;
    align-content: center;
    margin: 0 10% 40px 10%;
    width: 80%;
}

.user__info__point {
    display: flex;
}

.user__info__point input {
    color: #7D87A9;
}

.user__info__point input:focus {
    color: #53B9EA;
}

.user__info__point label {
    color: #7882A3;
}

#standard-basic:disabled {
    -webkit-text-fill-color: #656d88;
}

.user__info span {
    font-weight: bold;
    color: #bbbbbb;
}

.user__info__main {
    background: var(--background-user-info);
    width: 500px;
    height: 320px;
    margin-top: 50px;
    margin-left: 100px;
    border-radius: 20px;
    box-shadow: var(--user-info-main-box-shadow);
}

.subscribe__info {
    margin-top: 40px;
}

.user__info__all {
    display: flex;
    flex-direction: row;
    height: 150px;
    margin-top: 30px;
    justify-content: center;
}

.user__info__titles {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    align-content: center;
    color: #53B9EA;
}

.user__info__fields {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    margin-left: 30px;
}

.error__page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70%;
    font-family: 'Inria Sans', sans-serif;
    color: #53B9EA;
    font-weight: bold;
}

.error__page div {
    font-size: 30px;
}

.error__page div span {
    font-size: 40px;
}

.link__main:-webkit-any-link {
    text-decoration: none;
    color: var(--button-back-color);
    font-family: 'Inria Sans', sans-serif;
    font-size: 17px;
    position: absolute;
    top: 30px;
    left: 100px;
    z-index: 10;
}

.header__login__register {
    display: flex;
    align-items: center;
    margin-right: 50px;
}

.header__btns {
    display: flex;
    justify-content: center;
    flex-direction: row;
    z-index: 11;
}

.phone {
    width: 280px;
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    display: none;
}

.phone .logo__title {
    font-size: 23px;
}

.phone ion-icon {
    display: none;
}

.close__sidebar__mobile {
    display: none;
}


@media screen and (max-width: 480px) {
    .main__info {
        height: auto;
    }

    .header {
        font-size: 10px;
    }

    .header__login__register {
        margin-right: 30px;
    }

    .sidebar {
        box-shadow: 0 0 20px #000000;
    }

    .close__sidebar__mobile {
        position: absolute;
        display: flex;
        left: 20px;
        top: 20px;
        width: 30px;
        height: 30px;
        font-size: 40px;
        color: #53B9EA;
    }

    .mobile__active {
        display: flex;
    }

    .phone .close__sidebar__mobile ion-icon {
        display: flex;
    }
    
}

@media screen and (max-width: 850px) {
    .sidebar__close {
        display: none;
    }

    .close__sidebar__mobile {
        position: absolute;
        display: flex;
        left: 20px;
        top: 20px;
        width: 30px;
        height: 30px;
        font-size: 40px;
        color: #53B9EA;
    }

    .mobile__active {
        display: flex;
    }

    .phone .close__sidebar__mobile ion-icon {
        display: flex;
        font-size: 20px;
        position: absolute;
        top: 12px;
    }

    .profile__title {
        width: 100vw;
        text-align: center;
        margin: 40px 0 0 0;
    }

    .user__info__main {
        background: var(--background-user-info);
        width: 100vw;
        height: 320px;
        min-width: 320px;
        max-width: 500px;
        margin: 50px 0 0 0;
        border-radius: 20px;
        box-shadow: var(--user-info-main-box-shadow);
    }

    .profile {
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

}

@media screen and (max-width: 1000px) {
    .sidebar {
        box-shadow: 0 0 10px #000000;
        position: absolute;
        height: 100%;
    }

    .sidebar__close {
        box-shadow: none;
    }
}