.premium__list {
    height: 70%;
    display: flex;
    color: #7889AE;
    align-items: center;
    font-family: 'Inria Sans', sans-serif;
    justify-content: space-evenly;
    width: 80%;
    margin: 50px 10%;
}

.premium__weak__twoweak {
    background: var(--premium-background-color);
    width: 300px;
    height: 75vh;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    box-shadow: 0 0 2px #020202;
    margin-left: 20px;
}

.premium__title {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
}

.premium__price {
    font-size: 25px;
    font-weight: bold;
    letter-spacing: 1px;
    color: #53B9EA;
}

.premium__buy {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.premium__buy__btn {
    width: 170px;
    height: 40px;
    margin-top: 20px;
    background: #53b9e5;
}

.premium__buy__btn:hover {
    background: #70bee3;
    transition: 0.3s;
}

.premium__buy button {
    border: none;
    outline: none;
    background: none;
    color: #252F47;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: bold;
    width: 100%;
    height: 100%;
    cursor: pointer;
    box-shadow: 0 0 10px #53B9EA;
}

.buy__form {
    width: 250px;
    height: 100%;
    /* border: 2px dashed #53b9e5; */
    margin: 0 5px;
    /* box-shadow: 0 0 10px #53B9EA; */
    background: var(--premium-background-color);
    /* box-shadow: 0 0 4px #020202; */
    /* backdrop-filter: blur(20px);
    border-radius: 20px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: var(--premium-box-shadow);
}

.premium__buy__btn button:disabled {
    background: var(--button-premium-disabled);
    box-shadow: none;
    color: #7889AE;
}

.select__active {
    color: #7889AE;
    font-size: 10px;
}

.select__active div {
    color: #7889AE;
    font-size: 14px;
    text-align: center;
    align-items: center;
}

.select__active div input {
    text-align: center;
}

.buy__form ion-icon {
    margin-top: 50px;
    font-size: 40px;
    color: #53B9EA;
}

@media screen and (max-width: 480px) {
    .premium__list {
        flex-direction: column;
        margin-top: 0;
    }

    .premium__weak__twoweak {
        min-width: 250px;
        max-width: 400px;
        width: 100%;
        height: 500px;
        margin-top: 20px;
        margin-left: 0;
    }
}