html[data-theme='light'] {
    --handler-background: #ffffff;
    --handler-box-shadow: none;
    --handler-border: 1px solid #414A71;
}

html[data-theme='dark'] {
    --handler-background: #53B9EA;
    --handler-box-shadow: 0 0 15px #53B9EA;
    --handler-border: none;
}


.handler {
    width: 60px;
    height: 25px;
    background-color: var(--handler-background);
    border: var(--handler-border);
    border-radius: 20px;
    margin: 30px;
    transition: background-color 100ms linear;
    box-shadow: var(--handler-box-shadow);
    display: flex;
    cursor: pointer;
    z-index: 10;
}

@media screen and (max-width: 480px) {
    .handler {
        margin-right: 20px;
    }

    .handler__btn {
        width: 50px;
        height: 20px;
    }

    .handler__login__register {
        margin-right: 10px;
    }

    .handler__login__register a {
        font-size: 10px;
    }
}