.login__main {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Inria Sans', sans-serif;  
}

.login__form {
    width: 300px;
    height: 400px;
    background: var(--login-background);
    border-radius: 10px;
    box-shadow: 0 0 1px #252F47;
}

.login__title {
    font-size: 25px;
    font-family: 'Inria Sans', sans-serif;
    color: #a9a9a9;
    font-weight: 700;
    text-align: center;
    margin-top: 50px;
}

.fields {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
}

.register__main {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.register__form {
    width: 600px;
    height: 480px;
    background: var(--login-background);
    border-radius: 10px;
    box-shadow: 0 0 1px #252F47;
}

.register__title {
    font-size: 25px;
    font-family: 'Inria Sans', sans-serif;
    color: #a9a9a9;
    font-weight: 700;
    text-align: center;
    margin-top: 50px;
}

.fields {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
}

.register__link {
    color: red;
}

.register__fields {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
}

.submit__register {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.error_messages {
    position: absolute;
    right: 20px;
    bottom: 20px;
}

.error_messages div {
    margin-top: 5px;
}

.telegram__link {
    background: none;
    border: none;
    outline: 1px solid #1A73CD;
    font-size: 15px;
    width: 20px;
    height: 20px;
    border-radius: 2px;
    cursor: pointer;
    margin-left: 0px;
}

.telegram__link a {
    text-decoration: none;
    color: #1A73CD;
    width: 20px;
    height: 20px;
}

.telegram__link a:hover {
    text-decoration: none;
    color: #ffffff;
    width: 20px;
    height: 20px;
}

.telegram__link a ion-icon {
    width: 20px;
    height: 20px;
}

.telegram__link:hover {
    outline: 1px solid #1A73CD;
    background: #1A73CD;
    color: white;
    transition: 200ms;
}