@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

html[data-theme='light'] {
    --font-color: #454C77;
}

html[data-theme='dark'] {
    --font-color: #828CAE;
}

.sidebar__btn {
    width: 100%;
    height: 60px;
    cursor: pointer;
    color: var(--font-color);
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
    font-weight: bold;
    font-family: 'Ubuntu', sans-serif;
    padding-left: 40px;
}

.sidebar__btn span {
    text-align: center;
}

.sidebar__btn__active {
    color: #53B9EA;
}

.text:hover {
    color: #5b6792;
}