.edit__btn {
    background: none;
    border: none;
    outline: 1px solid #d34040;
    color: #D32F2F;
    font-size: 15px;
    width: 25px;
    height: 25px;
    border-radius: 2px;
    cursor: pointer;
    margin-left: 5px;
}

.edit__btn:hover {
    outline: 1px solid #D32F2F;
    background: #c43a3a;
    color: white;
    transition: 200ms;
}