.back__shadow {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: 1s;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 15;
}

.form {
    width: 500px;
    height: 250px;
    background: var(--password-recovery-background);
    opacity: 1;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-family: 'Inria Sans', sans-serif;
}

.title {
    color: #828BAA;
    font-size: 20px;
    font-weight: bold;
}

.message {
    color: #717892;
    text-align: center;
}

.message p {
    font-size: 18px;
    color: #1D6CBB;
    margin-top: 5px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.error {
    color: red;
    font-size: 12px;
}

.alert__msg {
    width: 300px;
    position: absolute;
    right: 30px;
    bottom: 30px;
}