.partnership__main {
    display: flex;
    justify-content: center;
    flex-direction: row;
    font-family: 'Inria Sans', sans-serif;
    margin-right: 100px;
}

.partnership__info {
    width: 500px;
    height: 550px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.value {
    width: 350px;
    height: 200px;
    background: var(--partnership-elements-background);
    margin-top: 30px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.value__title {
    color: #7F8CAD;
    font-weight: bold;
    font-size: 15px;
    width: 120px;
}

.value__number {
    color: #7F8CAD;
    font-weight: bold;
    font-size: 30px;
    border: 2px solid #869FE8;
    width: 130px;
    height: 130px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 5px #869FE8;
}

.personal__link, .profit {
    width: 350px;
    height: 80px;
    background: var(--partnership-elements-background);
    margin-top: 20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: #7F8CAD;
}

.link__title, .profit__title {
    font-weight: bold;
    width: 120px;
}

.link, .profit__number {
    width: 134px;
    text-align: center;
}

.partnership__referals {
    width: 500px;
    height: 200px;
    background: var(--partnership-elements-background);
    margin-top: 30px;
    overflow-y: auto;
    border-radius: 10px;
}

.profit__number {
    color: #53B9EA;
}

.user__referal {
    width: 80%;
    margin: 10px 10%;
    border-radius: 10px;
    height: 30px;
    background: var(--partnership-elements-background);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.user__name {
    color: #7F8CAD;
}

.user__profit {
    color: rgb(0, 193, 0);
}

.referals__main {
    display: flex;
    flex-direction: column;
}

.manual {
    width: 500px;
    height: 180px;
    margin-top: 20px;
    background: var(--partnership-elements-background);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.box__shadow {
    box-shadow: 0 0 1px #252F47;
}

.manual__title {
    color: #7F8CAD;
    font-weight: bold;
    font-size: 17px;
}

.manual__steps {
    color: #7F8CAD;
    font-size: 15px;
}

.manual__step {
    margin-top: 5px;
}

.partnership__referals::-webkit-scrollbar {
    width: 2px;
}

.partnership__referals::-webkit-scrollbar-track {
    background: var(--partnership-elements-background);
}

.partnership__referals::-webkit-scrollbar-thumb {
    background-color: #53B9EA;
}

@media screen and (max-width: 850px) {
    .partnership__main {
        display: flex;
        flex-direction: column;
        font-family: 'Inria Sans', sans-serif;
        align-items: center;
        margin-right: 0;
    }

    .partnership__info {
        width: 320px;
        height: 400px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .partnership__referals {
        width: 320px;
    }

    .manual {
        width: 320px;
    }
}