@import url('https://fonts.googleapis.com/css2?family=Inria+Sans:wght@700&display=swap');

.sub__inactive {
    width: 160px;
    margin-left: 100px;
    margin-top: 30px;
    font-family: 'Inria Sans', sans-serif;
    color: #74C6EE;
    font-size: 17px;
    text-transform: uppercase;
}

.status {
    /* border: 1px solid #74C6EE; */
    text-align: center;
    height: 30px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    color: #7F8BAE;
    margin-bottom: 20px;
}

.inactive {
    color: rgb(225, 52, 52);
}

.active {
    color: rgb(0, 187, 0);
}

.test__btn {
    background: green;
    color: white;
    outline: none;
    border: none;
    cursor: pointer;
}



@media screen and (max-width: 850px) {
    .sub__inactive {
        width: 100%;
        margin-top: 30px;
        margin-left: 0;
        text-align: center;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .status {
        justify-content: normal;
    }
}