.contacts {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 50px;
}

.info__link {
    width: 240px;
    height: 150px;
    margin: 20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.info__link ion-icon {
    font-size: 20px;
    margin-right: 10px;
    color: var(--main-logo-color);
}

.block__name {
    display: flex;
    align-items: center;
    transition: 0.5s;
}

.contact__value {
    position: absolute;
    font-family: 'Inria Sans', sans-serif;
    color: #c4c8d5;
    font-size: 15px;
    opacity: 0;
    transition: 0.5s;
}

.telegram {
    background: linear-gradient(115.48deg, #4f6094 2.82%, #1b748f 94.92%);
    background: var(--contacts-background);
    box-shadow: var(--user-info-main-box-shadow);
}

.telegram svg {
    margin-right: 5px;
}

.mail {
    box-shadow: var(--user-info-main-box-shadow);
    background: linear-gradient(115.48deg, #4f6094 2.82%, #1b748f 94.92%);
    background: var(--contacts-background);
}

.youtube {
    box-shadow: var(--user-info-main-box-shadow);
    background: #c4302b;
    background: linear-gradient(115.48deg, #4f6094 2.82%, #1b748f 94.92%);
    background: var(--contacts-background);
}

.youtube ion-icon {
    color: #c4302b;
}

.telegram__chat {
    background: linear-gradient(115.48deg, #4f6094 2.82%, #1b748f 94.92%);
    background: var(--contacts-background);
    box-shadow: var(--user-info-main-box-shadow);
}

.telegram__chat svg {
    margin-right: 5px;
}

.title {
    color: var(--contact-title-color);
    font-family: 'Inria Sans', sans-serif;
    font-size: 20px;
}